import React, { Component } from "react";
import Link from "gatsby-link";
import SkillsList from '../../components/SkillsList';
import SideNav from "../../components/SideNav/SideNav";
import { PageHeader } from "react-bootstrap";

class DocsSkills extends Component {
    constructor(props) {
        super(props);
        const data = this.props.data;
        this.state = {
            initialData: {...data},
            data: {}
        };
    }

    componentWillMount() {
        this.setState({data: {...this.state.initialData}});
    }

    handleFilterList(event) {
        let updatedList = {...this.state.initialData};
        updatedList.allMarkdownRemark.edges = updatedList.allMarkdownRemark.edges.filter(item => {
            return item.node.frontmatter.title.toLowerCase().search(
                event.target.value.toLowerCase()) !== -1;
        });
        this.setState({data: updatedList});
    }

    handleSearchList(event) {
        let value = event.target.value.toLowerCase();
        console.log(value);
        $("#skills div").filter(function() {
            $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1);
        })
    }

    render() {
        return (
            <div>
                <SideNav />
                <div className="document">
                    <PageHeader>
                        Skills List
                    </PageHeader>
                    <div className="col-lg-12">
                        <div className="filter-list">
                            <form>
                                <fieldset className="form-group">
                                    <input type="text" className="form-control form-control-lg" placeholder="Search..." onKeyUp={(e) => this.handleSearchList(e)}/>
                                </fieldset>
                            </form>
                        </div>
                    </div>
                    <div id="skills">
                        <SkillsList skills={this.state.data} />
                    </div>
                </div>    
            </div>
        );
    }
}



export default DocsSkills;
    

export const query = graphql `
query SkillsList {
    allMarkdownRemark (
        sort: {fields: [frontmatter___group, frontmatter___level, frontmatter___title], order: ASC},
        filter: {fileAbsolutePath: {regex: "/(docs)/(skills)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
                frontmatter {
                    title
                    syntax
                    group
                    level
                    offensive
                }
            }
        }
    }
}
`


